.App {
  text-align: center;
  position: relative;
  overflow: hidden;
  height: 100vh; /* Full viewport height */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-family: 'Arial', sans-serif;
  background: #f0f0f0;
  border: 2px solid green;
  margin: 5px;
}

.bubble {
  position: absolute;
  bottom: -150px; /* Start a bit off screen */
  background-color: var(--bubble-color, green);
  border-radius: 50%;
  opacity: 0.7;
  pointer-events: none; /* Make clicks go through the bubble */
  animation: rise 10s infinite; /* Default animation for all bubbles */
}

@keyframes rise {
  to {
    transform: translateY(-110vh);
    opacity: 0;
  }
}

/* Bubble sizes and animation delays are now removed since they should be handled by JavaScript */

button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

p {
  font-weight: bold;
  font-family: cursive;
  color: #333;
  /* border: 1px solid #ddd; */
  padding: 10px;
  border-radius: 8px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  font-size: 23px;
}
h5{
  font-style: italic;
}
